import React from "react";
import { IMG_CDN_URL } from "../utils/constants";

const MovieCard = ({ posterPath }) => {
  if (!posterPath) return null;
  return (
    <div className="w-36 md:w-48 pr-4">
      <div>
        <img
          className="rounded"
          alt="Movie Card"
          src={IMG_CDN_URL + posterPath}
        />
      </div>
    </div>
  );
};

export default MovieCard;
